import {hashSHA256, X} from "./hashSHA256";
import { db } from "./firebase";
import {doc, deleteDoc, collection, getDocs, addDoc, updateDoc} from "firebase/firestore";

export const checkHash = async t => {
    const V = await hashSHA256(t);
    return V === X;
};

export const addOrg = async (orgData) => {
    try {
        const orgCollection = collection(db, "organizations");
        const docRef = await addDoc(orgCollection, orgData);
        console.log(` , ID: ${docRef.id}`);
        return docRef.id; //  ID  
    } catch (error) {
        console.error("   : ", error);
        throw error; //   
    }
};

export const removeOrg = async (orgId) => {
    try {
        const orgDoc = doc(db, "organizations", orgId);
        await deleteDoc(orgDoc);
        console.log(`  ID: ${orgId}  .`);
    } catch (error) {
        console.error("   : ", error);
        throw error;
    }
};

export const editOrg = async (orgId, updatedData) => {
    try {
        const orgDoc = doc(db, "organizations", orgId);
        await updateDoc(orgDoc, updatedData);
        console.log(`  ID: ${orgId}  .`);
    } catch (error) {
        console.error("   : ", error);
        throw error;
    }
};

export const getOrgs = async () => {
    try {
        const orgCollection = collection(db, "organizations"); //  
        const snapshot = await getDocs(orgCollection); //    Firestore
        const organizations = snapshot.docs.map((doc) => ({
            id: doc.id, // ID 
            ...doc.data(), //  
        }));
        console.log(" : ", organizations);
        return organizations; //    
    } catch (error) {
        console.error("   : ", error);
        throw error;
    }
};

export const toNormalDate = milliseconds => {
    const date = new Date(milliseconds);
    const day = String(date.getDate()).padStart(2, '0');
    const month = String(date.getMonth() + 1).padStart(2, '0'); //   JavaScript   0
    const year = date.getFullYear();
    return `${day}.${month}.${year}`;
}

export const DDMMYY_Date = timeInMs => {
    const date = new Date(timeInMs);
    const m = date.getMonth();
    const month = (m + 1) < 10 ? '0' + (m + 1) : m + 1;
    const d = date.getDate();
    const day = d < 10 ? '0' + d : d;
    return `${date.getFullYear()}-${month}-${day}`;
}