import React, { useEffect, useState } from 'react';
import { Button, Modal, Form } from "react-bootstrap";
import {DDMMYY_Date, editOrg, removeOrg, toNormalDate} from "../js/utils";
import { KEYS_DESCRIPTIONS } from "../js/refs";
import { TEXTS } from "../js/text";

const MOrgTemplate = (props) => {
    const [showEditModal, setShowEditModal] = useState(false);
    const [showOrgRemovedModal, setShowOrgRemovedModal] = useState(false);
    const [showAdmDir, setShowAdmDir] = useState(false);
    const [showModalRemoveOrg, setShowModalRemoveOrg] = useState(false);
    const [isFirstRender, setIsFirstRender] = useState(true);
    const {
        orgInfo,
        i,
        onOrgRemoved //          
    } = props;

    //     
    const [updatedOrg, setUpdatedOrg] = useState(orgInfo);

    const SHORT_ORG_NAME = (
        updatedOrg.orgName &&
            updatedOrg.orgName.split(',')[0]
        ) || '';
    if (updatedOrg.orgName == undefined) {
        console.log(updatedOrg)
    }
    useEffect(() => {
        if (isFirstRender) {
            setIsFirstRender(false);
            return;
        }
        //    ,  
        // editOrg(updatedOrg.id, updatedOrg).then(() => {
        //     console.log("Organization updated successfully!");
        // }).catch((error) => {
        //     console.error("Error updating organization:", error);
        // });
    }, [updatedOrg]); //     updatedOrg

    //   
    const handleCheckboxChange = () => {
        setUpdatedOrg(prevOrg => ({
            ...prevOrg,
            isHidden: !prevOrg.isHidden //   isHidden
        }));
        editOrg(updatedOrg.id, updatedOrg).then(() => {
            console.log("Organization updated successfully!");
        }).catch((error) => {
            console.error("Error updating organization:", error);
        });
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setUpdatedOrg(prevOrg => ({
            ...prevOrg,
            [name]: value
        }));
    };

    const handleSaveChanges = () => {
        editOrg(updatedOrg.id, updatedOrg)
            .then(() => {
                console.log("Organization updated successfully!");
                setShowEditModal(false);
            })
            .catch((error) => {
                console.error("Error updating organization:", error);
            });
        setShowEditModal(false);
    };

    //   
    const handleRemoveOrg = () => {
        removeOrg(updatedOrg.id)
            .then(() => {
                console.log("Organization removed successfully!");
                onOrgRemoved(updatedOrg.id); //      
                setShowModalRemoveOrg(false); //   
                setShowOrgRemovedModal(true);
            })
            .catch((error) => {
                console.error("Error removing organization:", error);
            });
    };
    const normalEndDate = toNormalDate(updatedOrg.agreementEndDate);
    return (
        <tr>
            <td>
                {i + 1}
                <Button
                    variant="outline-danger"
                    onClick={() => setShowModalRemoveOrg(true)}
                >
                    <i className="bi bi-ban"></i>
                </Button>
                <Button
                    className="mt-2"
                    variant="outline-info"
                    onClick={() => setShowEditModal(true)}
                >
                    <i className="bi bi-wrench"></i>
                </Button>
            </td>
            <td>{SHORT_ORG_NAME}</td>
            <td>{toNormalDate(updatedOrg.agreementStartDate)}</td>
            <td>
                {
                    normalEndDate.includes('NaN') || normalEndDate.includes('1970')
                        ? updatedOrg.agreementEndDate
                        : normalEndDate
                }
            </td>
            {/*<td>{updatedOrg.longTermCooperationAgreementNumber}</td>*/}
            {/*<td>{updatedOrg.cooperationAgreement}</td>*/}
            <td>{updatedOrg.practicalTrainingAgreementNumber}</td>
            <td>{updatedOrg.practicalTrainingContract}</td>
            <td className="vertAlMiddle">
                <Button
                    variant="primary"
                    onClick={() => setShowAdmDir(admDir => !admDir)}
                >
                    <i className="bi bi-eye"></i>
                </Button>
                <br/>
                {updatedOrg.admissionDirections}
            </td>
            <td className="vertAlMiddle">
                <Form>
                    <Form.Check
                        type="checkbox"
                        className="fs-3"
                        checked={updatedOrg.isHidden}
                        onChange={handleCheckboxChange} //   
                    />
                </Form>
            </td>
            <Modal show={showAdmDir} onHide={() => setShowAdmDir(admDir => !admDir)}>
                <Modal.Header closeButton>
                    <Modal.Title>{KEYS_DESCRIPTIONS.admissionDirections}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {updatedOrg.admissionDirections}
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowAdmDir(admDir => !admDir)}
                    >
                        {TEXTS.CLOSE}
                    </Button>
                </Modal.Footer>
            </Modal>
            <Modal show={showModalRemoveOrg} onHide={() => setShowModalRemoveOrg(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>{TEXTS.REMOVING_ORG}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {TEXTS.ARE_YOU_SURE_YOU_WANT_TO_REMOVE_ORG.replace(
                        '{orgName}', SHORT_ORG_NAME
                    )}
                </Modal.Body>
                <Modal.Footer>
                    <Button
                        variant="secondary"
                        onClick={() => setShowModalRemoveOrg(false)}
                    >
                        {TEXTS.CANCEL}
                    </Button>
                    <Button
                        variant="outline-danger"
                        onClick={handleRemoveOrg}
                    >
                        {TEXTS.REMOVE}
                    </Button>
                </Modal.Footer>
            </Modal>
            <Modal show={showOrgRemovedModal} onHide={() => setShowOrgRemovedModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>{TEXTS.ORG_REMOVED}</Modal.Title>
                </Modal.Header>
                <Modal.Footer>
                    <Button
                        variant="success"
                        onClick={() => setShowOrgRemovedModal(false)}
                    >
                        {TEXTS.OK}
                    </Button>
                </Modal.Footer>
            </Modal>
            <Modal show={showEditModal} onHide={() => setShowEditModal(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>{TEXTS.EDITING_ORG}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Form>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.orgName}</Form.Label>
                            <Form.Control
                                type="text"
                                name="orgName"
                                value={updatedOrg.orgName || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formAgreementStartDate">
                            <Form.Label>{KEYS_DESCRIPTIONS.agreementStartDate}</Form.Label>
                            <Form.Control
                                type="date"
                                name="agreementStartDate"
                                value={DDMMYY_Date(updatedOrg.agreementStartDate) || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formAgreementEndDate">
                            <Form.Label>{KEYS_DESCRIPTIONS.agreementEndDate}</Form.Label>
                            <Form.Control
                                type="date"
                                name="agreementEndDate"
                                value={DDMMYY_Date(updatedOrg.agreementEndDate) || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.longTermCooperationAgreementNumber}</Form.Label>
                            <Form.Control
                                type="text"
                                name="longTermCooperationAgreementNumber"
                                value={updatedOrg.longTermCooperationAgreementNumber || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.cooperationAgreement}</Form.Label>
                            <Form.Control
                                type="text"
                                name="cooperationAgreement"
                                value={updatedOrg.cooperationAgreement || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.practicalTrainingAgreementNumber}</Form.Label>
                            <Form.Control
                                type="text"
                                name="practicalTrainingAgreementNumber"
                                value={updatedOrg.practicalTrainingAgreementNumber || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.practicalTrainingContract}</Form.Label>
                            <Form.Control
                                type="text"
                                name="practicalTrainingContract"
                                value={updatedOrg.practicalTrainingContract || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                        <Form.Group controlId="formOrgName">
                            <Form.Label>{KEYS_DESCRIPTIONS.admissionDirections}</Form.Label>
                            <Form.Control
                                type="text"
                                name="admissionDirections"
                                value={updatedOrg.admissionDirections || ''}
                                onChange={handleInputChange}
                            />
                        </Form.Group>
                    </Form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={() => setShowEditModal(false)}>
                        {TEXTS.CANCEL}
                    </Button>
                    <Button variant="primary" onClick={handleSaveChanges}>
                        {TEXTS.SAVE}
                    </Button>
                </Modal.Footer>
            </Modal>

        </tr>
    );
};

export default MOrgTemplate;
